import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import AnimationManagerTab from './AnimationManagerTab'
import { Button } from '@mui/material';
function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`vertical-tabpanel-${index}`}
        aria-labelledby={`vertical-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            {children}
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };



  function a11yProps(index) {
    return {
      id: `vertical-tab-${index}`,
      'aria-controls': `vertical-tabpanel-${index}`,
    };
  }



export default function AnimationMakerPage({terminal,ledsettings}) {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    const eventManager = { SaveEvent:()=>{},LoadEvent:()=>{} };

    return (
      <Box sx={{  bgcolor: 'background.paper', display: 'flex', paddingBottom:3 }}>

        <Box>
            <Tabs
            orientation="vertical"
            variant="scrollable"
            value={value}
            onChange={handleChange}
            aria-label="Vertical tabs example"
            sx={{ borderRight: 1, borderColor: 'divider' }}
            >
                <Tab label="Animation One" {...a11yProps(0)} />
            </Tabs>
        </Box>

        <Box>
            <TabPanel value={value} index={0}>
                <div><Button onClick={()=>eventManager.SaveEvent()}>Send to device</Button>  <Button>Load from device</Button></div>
                <AnimationManagerTab animationID={"0"}  terminal={terminal} eventManager={eventManager}  />
            </TabPanel>
        </Box>

      </Box>
    );
      


};